getWindowMoveFolder();

function getWindowMoveFolder() {
    // Initialize the position variables
    let pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;

    // Attach the drag events to the header div
    windowMoveFolder.querySelector('.window-folder-top').onmousedown = dragMouseDown;

    function dragMouseDown(e) {
        e = e || window.event;
        e.preventDefault();
        // Get the mouse cursor position at startup
        pos3 = e.clientX;
        pos4 = e.clientY;
        document.onmouseup = closeDragElement;
        // Call a function whenever the cursor moves
        document.onmousemove = elementDrag;
    }

    function elementDrag(e) {
        e = e || window.event;
        e.preventDefault();
        // Calculate the new cursor position
        pos1 = pos3 - e.clientX;
        pos2 = pos4 - e.clientY;
        pos3 = e.clientX;
        pos4 = e.clientY;
        // Set the element's new position
        windowMoveFolder.style.top = (windowMoveFolder.offsetTop - pos2) + "px";
        windowMoveFolder.style.left = (windowMoveFolder.offsetLeft - pos1) + "px";
    }

    function closeDragElement() {
        // Stop moving when mouse button is released
        document.onmouseup = null;
        document.onmousemove = null;
    }
}

